@media (max-width: 1023px) {
  .btn-menu {
    width: 30px;
    height: 30px;
    background-color: rgba(0, 0, 0, 0);
    display: block;
    position: relative;
  }

  .btn-menu__icon, .btn-menu__icon:before, .btn-menu__icon:after {
    content: "";
    width: 100%;
    height: 2px;
    z-index: 1000;
    background-color: #000010;
    transition: top .1s ease-in;
    position: absolute;
    left: 0;
  }

  .btn-menu__icon {
    top: calc(50% - 1px);
  }

  .btn-menu__icon:after {
    top: 8px;
  }

  .btn-menu__icon:before {
    top: -8px;
  }

  .nav__list {
    display: none;
  }

  .menu-open .btn-menu__icon:before, .menu-open .btn-menu__icon:after {
    top: 0;
  }

  .menu-open .btn-menu {
    padding: 15px;
  }

  .menu-open .btn-menu__icon {
    transform: rotate(-45deg);
  }

  .menu-open .btn-menu__icon:after {
    transform: rotate(90deg);
  }

  .nav__list {
    width: 100%;
    height: 100%;
    z-index: 999;
    background: #fff linear-gradient(135deg, #fff 0%, rgba(255, 113, 91, .6) 100%);
    flex-direction: column;
    justify-content: space-between;
    row-gap: 10px;
    padding: 40px 26px;
    transition: transform .3s ease-in;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(-100%);
  }

  .menu-open .nav__list {
    transform: translateX(0);
  }

  .socialmedia {
    opacity: 0;
  }

  .menu-open .socialmedia {
    opacity: 1;
  }
}

.links__link {
  transition: border-bottom .35s ease-in-out, border-radius .35s ease-in-out;
}

